import * as Yup from 'yup';

/**
 * Firma oluturma formu iin validasyon emas
 * Veritaban emasna uygun olarak tasarlanmtr:
 * - company_name: VARCHAR(50), en az 2 karakter
 * - sector: VARCHAR(50), nullable
 * - currency: VARCHAR(3), byk harf ISO kodu
 */
export const createCompanyValidationSchema = Yup.object({
    company_name: Yup.string()
        .min(2, 'Firma ad en az 2 karakter olmaldr')
        .max(50, 'Firma ad en fazla 50 karakter olabilir')
        .required('Firma ad zorunludur')
        .trim(),
    sector: Yup.string()
        .max(50, 'Sektr en fazla 50 karakter olabilir')
        .nullable(),
    currency: Yup.string()
        .length(3, 'Para birimi kodu 3 karakter olmaldr')
        .matches(/^[A-Z]{3}$/, 'Para birimi kodu 3 byk harf olmaldr (rn: USD, EUR, TRY)')
        .required('Para birimi zorunludur')
});

/**
 * Firma gncelleme formu iin validasyon emas
 * Tm alanlar opsiyonel (sadece gnderilen alanlar gncellenir)
 */
export const updateCompanyValidationSchema = Yup.object({
    company_name: Yup.string()
        .min(2, 'Firma ad en az 2 karakter olmaldr')
        .max(50, 'Firma ad en fazla 50 karakter olabilir')
        .trim(),
    sector: Yup.string()
        .max(50, 'Sektr en fazla 50 karakter olabilir')
        .nullable(),
    currency: Yup.string()
        .length(3, 'Para birimi kodu 3 karakter olmaldr')
        .matches(/^[A-Z]{3}$/, 'Para birimi kodu 3 byk harf olmaldr (rn: USD, EUR, TRY)')
});

/**
 * Firma oluturma formu iin balang deerleri
 */
export const createCompanyInitialValues = {
    company_name: '',
    sector: '',
    currency: 'USD' // Veritaban varsaylan
};
