/**
 * Yaygn kullanlan para birimleri
 * ISO 4217 standartna uygun 3 harfli kodlar
 */
export const CURRENCY_OPTIONS = [
    { value: 'USD', label: 'USD - Amerikan Dolar' },
    { value: 'EUR', label: 'EUR - Euro' },
    { value: 'TRY', label: 'TRY - Trk Liras' },
    { value: 'GBP', label: 'GBP - ngiliz Sterlini' },
    { value: 'JPY', label: 'JPY - Japon Yeni' },
    { value: 'CHF', label: 'CHF - svire Frang' },
    { value: 'CAD', label: 'CAD - Kanada Dolar' },
    { value: 'AUD', label: 'AUD - Avustralya Dolar' },
    { value: 'CNY', label: 'CNY - in Yuan' },
    { value: 'RUB', label: 'RUB - Rus Rublesi' }
];

/**
 * Varsaylan para birimi
 */
export const DEFAULT_CURRENCY = 'USD';

/**
 * Para birimi format regex pattern
 */
export const CURRENCY_REGEX = /^[A-Z]{3}$/;

/**
 * Para birimi kodlarn liste olarak dndrr
 */
export const getCurrencyCodes = () => CURRENCY_OPTIONS.map(option => option.value);

/**
 * Para birimi kodu iin label dndrr
 * @param {string} code - Para birimi kodu (rn: 'USD')
 * @returns {string} - Label (rn: 'USD - Amerikan Dolar')
 */
export const getCurrencyLabel = (code) => {
    const currency = CURRENCY_OPTIONS.find(option => option.value === code);
    return currency ? currency.label : code;
};
