import {Navigate, useLocation} from 'react-router-dom';
import {useAuth} from '../contexts/AuthContext';

const AuthRoute = ({
                       children,
                       guest,
                       requireAuth = false,
                       requireGuest = false,
                       redirectTo = "/"
                   }) => {

    const {user} = useAuth(); // Auth durumunuzu buradan aln
    const isAuthenticated = Boolean(user);
    const location = useLocation();

    // Giri yapm kullanclarn girmemesi gereken sayfalar (guest sayfalar)
    if (requireGuest && isAuthenticated) {
        // Eer sign-in sayfasndaysa ve location.state'de from bilgisi varsa, oraya ynlendir
        if (location.pathname === '/sign-in' && location.state?.from) {
            return <Navigate to={location.state.from} replace/>;
        }
        return <Navigate to={redirectTo} replace/>;
    }

    // Giri yapmam kullanclarn girmemesi gereken sayfalar (protected sayfalar)
    if (requireAuth && !isAuthenticated) {
        // Giri yaptktan sonra tekrar bu sayfaya dnmek iin state ile location bilgisi gnder
        return <Navigate
            to="/sign-in"
            state={{from: location.pathname + location.search}}
            replace
        />;
    }

    // Eski API destei iin (mevcut kodunuzla uyumlu olmas iin)
    if (guest && isAuthenticated) {
        return <Navigate to={redirectTo} replace/>;
    }

    return children || guest;
};

export default AuthRoute;
