import * as yup from 'yup';
import {TEXT_ONLY_REGEX, USERNAME_REGEX, PASSWORD_REGEX, isValidEmail, hasMaxNumberCount} from '../utils/regex';

const signUpSchema = yup.object().shape({
    name: yup
        .string()
        .required('Ad bo braklamaz.')
        .min(3, 'Ad en az 3 karakter uzunluunda olmaldr.')
        .max(20, 'Ad en fazla 20 karakter uzunluunda olmaldr.')
        .matches(TEXT_ONLY_REGEX, 'Ad sadece harflerden olumaldr.'),

    surname: yup
        .string()
        .required('Soyad bo braklamaz.')
        .min(3, 'Soyad en az 3 karakter uzunluunda olmaldr.')
        .max(20, 'Soyad en fazla 20 karakter uzunluunda olmaldr.')
        .matches(TEXT_ONLY_REGEX, 'Soyad sadece harflerden olumaldr.'),

    username: yup
        .string()
        .required('Kullanc ad bo braklamaz.')
        .min(6, 'Kullanc ad en az 6 karakter uzunluunda olmaldr.')
        .max(15, 'Kullanc ad en fazla 15 karakter uzunluunda olmaldr.')
        .matches(USERNAME_REGEX, 'Kullanc ad yalnzca harf, rakam ve alt izgi ierebilir.')
        .test('max-numbers', 'Kullanc ad en fazla 4 rakam ierebilir.', (value) => {
            return hasMaxNumberCount(value, 4);
        }),
    // NOT: USERNAME_REGEX zaten zel karakter kontroln ieriyor,

    email: yup
        .string()
        .required('E-posta bo braklamaz.')
        .test('valid-email', 'Geerli bir e-posta adresi girilmelidir.', (value) => {
            return isValidEmail(value);
        }),

    password: yup
        .string()
        .required('ifre bo braklamaz.')
        .min(8, 'ifre en az 8 karakter uzunluunda olmaldr.')
        .max(20, 'ifre en fazla 20 karakter uzunluunda olmaldr.')
        .matches(PASSWORD_REGEX, 'ifre byk harf, kk harf, rakam ve zel karakter iermelidir.'),

    confirmpassword: yup
        .string()
        .required('ifre dorulamas bo braklamaz.')
        .oneOf([yup.ref('password'), null], 'ifreler elemiyor.')
});

export default signUpSchema;
